<template>
  <a-layout id="admin-layout" class="layout"
    ><a-layout-header
      style="text-align: left; line-height: 50px; height: 50px; padding: 0px 0px 0px 0px"
      ><a-row
        ><a-col :span="3"
          ><a-image
            src="/logo.svg"
            height="44px"
            weight="80px"
            style="margin-left: 0px;margin-top: 0px"/></a-col
        ><a-col :span="15"
          ><a-menu
            mode="horizontal"
            @click="handleSelect"
            :style="{
              lineHeight: '48px',
              fontWeight: 'bold',
              height: '40px',
              borderBottom: 'unset',
            }"
            ><a-menu-item key="NftWork" style="font-size: 18px; margin-top: 1px"
              >NftWork </a-menu-item
            ><a-menu-item key="MarketPlace" style="font-size: 18px;"
              >MarketPlace </a-menu-item
            ><a-menu-item key="Collection" style="font-size: 18px;"
              >Collection </a-menu-item
            ><a-menu-item key="Blog" style="font-size: 18px;">
              Blog
            </a-menu-item></a-menu
          ></a-col
        ><a-col :span="6" style="text-align: right">
          <a-dropdown v-if="getCurrentUser()">
            <a-space>
              <a-avatar
                v-if="getCurrentUser().head_avatar"
                :src="'/app/file/get/' + getCurrentUser().head_avatar"
                :size="28"
              /><a-avatar
                v-else
                :size="28"
                style="font-size: 18px; margin-top: 00px;"
                ><template #icon><UserOutlined /></template
              ></a-avatar>
              <a
                class="ant-dropdown-link hearder-text"
                @click="(e) => e.preventDefault()"
                style="font-size: 18px; margin-top: 00px;margin-right: 30px;color:#3291E6"
              >
                {{ getCurrentUser().nickname || getCurrentUser().username }}
              </a>
            </a-space>
            <template #overlay
              ><a-menu
                ><a-menu-item
                  ><a href="javascript:;" @click="userSetting"
                    >Setting</a
                  ></a-menu-item
                ><a-menu-item
                  ><a href="javascript:;" @click="logout"
                    >Logout</a
                  ></a-menu-item
                ></a-menu
              ></template
            >
          </a-dropdown>
          <a-button
            v-else
            type="link"
            class="hearder-text"
            @click="goLogin"
            style="font-size: 18px; margin-top: 10px;margin-right: 30px;color:#3291E6"
            >Login</a-button
          ></a-col
        ></a-row
      ></a-layout-header
    ><a-layout-content
      ><div
        :style="{ background: '#fff', padding: '24px', minHeight: '280px' }"
      >
        <NftWork v-if="current === 'NftWork'" /><Collection
          v-if="current === 'Collection'"
        /><MarketPlace v-if="current === 'MarketPlace'" /><Blog
          v-if="current === 'Blog'"
        /></div></a-layout-content
    ><a-layout-footer style="text-align: center"
      >Ginkgo Nft ©2020 Created by ginkgo
    </a-layout-footer></a-layout
  ><a-modal
    :visible="showUserModal"
    title="User Setting"
    @ok="handleOk"
    @cancel="showUserModal = false"
    ><a-form
      ref="form"
      :model="form"
      :label-col="{ span: 4 }"
      :wrapper-col="{ span: 18 }"
      ><a-form-item label="Username" name="username"
        ><a-input v-model:value="form.username" disabled/></a-form-item
      ><a-form-item label="Nickname"
        ><a-input v-model:value="form.nickname"/></a-form-item
      ><a-form-item label="Avatar"
        ><a-upload
          v-model:fileList="fileList"
          name="file"
          list-type="picture-card"
          class="avatar-uploader"
          :show-upload-list="false"
          action="/app/file/upload"
          @change="handleAvatarChange"
          ><a-avatar
            v-if="form.head_avatar"
            :src="'/app/file/get/' + form.head_avatar"
            shape="square"
            :size="100"
          />
          <div v-else>
            <loading-outlined v-if="loading" /><plus-outlined v-else />
            <div class="ant-upload-text">Upload</div>
          </div></a-upload
        ></a-form-item
      ></a-form
    ></a-modal
  >
</template>
<script>
import NftWork from "@/components/NftWork";
import Collection from "@/components/Collection";
import MarketPlace from "@/components/MarketPlace";
import Blog from "@/components/Blog";

import {
  UserOutlined,
  PlusOutlined,
  LoadingOutlined,
} from "@ant-design/icons-vue";

import { mapGetters, mapActions } from "vuex";

import { message } from "ant-design-vue";
import api from "@/api/user";

export default {
  components: {
    NftWork,
    Collection,
    MarketPlace,
    Blog,
    UserOutlined,
    PlusOutlined,
    LoadingOutlined,
  },

  data() {
    return {
      current: "NftWork",
      form: {},
      showUserModal: false,
      fileList: [],
      loading: false,
    };
  },

  computed: {
    ...mapGetters(["getCurrentUser"]),
  },

  methods: {
    ...mapActions(["setCurrentUser"]),
    handleSelect(item) {
      this.current = item.key;
    },

    userSetting() {
      this.form = {
        ...this.getCurrentUser(),
      };
      this.showUserModal = true;
    },

    handleAvatarChange(info) {
      if (info.file.status === "uploading") {
        this.loading = true;
        return;
      }

      if (info.file.status === "done") {
        this.form.head_avatar = info.file.response;
        this.loading = false;
      }
    },
    handleOk() {
      let data = {
        ...this.form,
      };
      api.update(data).then((res) => {
        if (res.data.code === 1) {
          this.setCurrentUser(data);
          this.showUserModal = false;
        } else {
          message.error(res.data.description);
        }
      });
    },

    goLogin() {
      location.href = "/#/login";
    },

    logout() {
      api.logout().then((res) => {
        if (res.data.code === 1) {
          this.setCurrentUser(null);
        } else {
          message.error(res.data.description);
        }
      });
    },
  },

  mounted() {},
};
</script>
<style lang="less">
#admin-layout {
  min-height: 100%;
  .ant-layout-header {
    border-bottom: 1px solid #f0f0f0;
    background: #ffffff;
  }
  .hearder-text {
    color: rgba(0, 0, 0, 0.85);
    font-weight: bold;
  }
}
</style>
